.homeCategories{max-width:320px;width:100%;padding:30px 20px}.homeCategories__inner{width:100%;margin:0 auto}.homeCategories__title{font-size:16px;color:#032c41;font-weight:700;margin-bottom:20px}.homeCategories__content{display:grid;grid-template-columns:repeat(2,1fr);gap:40px 30px;align-items:center;justify-content:center}@media (min-width:320px){.homeCategories{max-width:480px}.homeCategories__title{margin-bottom:20px}}@media (min-width:480px){.homeCategories{max-width:720px}.homeCategories__content{grid-template-columns:repeat(3,1fr)}}@media (min-width:720px){.homeCategories{max-width:1000px;padding:30px 50px}.homeCategories__title{font-size:20px}.homeCategories__content{grid-template-columns:repeat(3,1fr);gap:58px 50px}}@media (min-width:1000px){.homeCategories{max-width:1280px}.homeCategories__content{grid-template-columns:repeat(4,1fr)}}@media (min-width:1280px){.homeCategories{max-width:1440px}.homeCategories__inner{max-width:1000px}.homeCategories__content{display:grid;grid-template-columns:repeat(6,1fr);gap:58px 50px}}@media (min-width:1440px){.homeCategories{max-width:100%}.homeCategories__inner{max-width:1220px}.homeCategories__content{display:grid;grid-template-columns:repeat(6,1fr);gap:58px 50px}}